import React, { createRef, useEffect } from 'react';
import { MousePosition } from '@hooks/useMousePointer';
import styles from './mouse-tracker.module.scss';

type MouseTrackerProps = {
  mousePosition: MousePosition;
};
const MouseTracker = ({
  mousePosition = { x: null, y: null, isOver: false },
}: MouseTrackerProps) => {
  const mouseRef = createRef<HTMLDivElement>();

  useEffect(() => {
    if (mouseRef.current) {
      const { x, y } = mousePosition;
      mouseRef.current.style.transform = `translate3d(${x}px, ${y}px,0px)`;
      // mouseRef.current.style.left = `${x}px`;
      // mouseRef.current.style.top = `${y}px`;
    }
  }, [mousePosition.x, mousePosition.y]);

  return <div className={styles['mouse-pointer']} ref={mouseRef} />;
};

export default MouseTracker;
